h2,
h3,
h4,
h5,
h6 {
  margin-top: 1.6em;
  position: relative;
}

h1 .anchor,
h2 .anchor,
h3 .anchor,
h4 .anchor,
h5 .anchor,
h6 .anchor {
  display: block;
  width: 1em;
  left: -1em;
  height: 100%;
  position: absolute;
}

h1:hover .anchor,
h2:hover .anchor,
h3:hover .anchor,
h4:hover .anchor,
h5:hover .anchor,
h6:hover .anchor {
