}

table {
  border: 1px solid #cfcfcf;
  border-collapse: collapse;
}

th {
  font-weight: 600;
}

td,
th {
  padding: 0.2em 0.4em;
  border: 1px solid #cfcfcf;
}

thead tr,
tbody tr:nth-child(even) {
  background-color: #f7f7f7;
}

thead tr:hover,
tbody tr:hover {
  background-color: #cfcfcf;
}

.dragover {
  opacity: 0.5;
}

