    margin: 0.6rem 0;
    padding: 0 0.5em;
    color: #fff;
    font-size: 0.9rem;
    line-height: 2;
    background-color: hsla(0, 0%, 100%, 0.15);
    border: 0;
    border-radius: 0.2em;
  }

