.error {
  display: table;
  max-width: 28rem;
  height: 6rem;
  margin: 32vh auto 0 auto;
  padding-left: 8.5rem;
  background: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512 512'%3E %3Cpath d='M507 426L283 54a31 31 0 00-54 0L5 426a31 31 0 0026 48h450a31 31 0 0026-48zM256 167c13 0 24 8 24 20 0 40-5 96-5 136 0 10-11 14-19 14-10 0-19-4-19-14 0-40-5-96-5-136 0-12 11-20 24-20zm0 244a25 25 0 010-51c14 0 26 12 26 26 0 13-12 25-26 25z'/%3E %3C/svg%3E") no-repeat;
  background-position: left top;
  background-size: 6rem 6rem
}

.error summary {
    display: block;
    font-size: 1.13em;
    cursor: pointer
  }

.error summary::-webkit-details-marker {
      display: none;
    }

.error summary::after {
      /* Expand arrow after the summary text. */
      display: inline-block;
      width: 0.7em;
      height: 0.7em;
      margin-left: 0.5em;
      background: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 444.819 444.819'%3E %3Cpath d='M434 114l-21-21c-8-7-16-11-26-11s-19 4-26 11L222 232 84 93a37 37 0 00-52 0l-21 21c-7 7-11 16-11 26s4 19 11 26l186 186a35 35 0 0052 0l185-186a37 37 0 000-52z'/%3E %3C/svg%3E") no-repeat center / contain;
      content: '';
    }

.error details[open] summary::after {
    /* Collapse arrow after the summary text. */
    background: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 445 445'%3E %3Cpath d='M434 279L248 93c-7-7-16-11-26-11s-18 4-25 11L11 279c-7 7-11 16-11 26s4 18 11 25l21 22c7 7 16 11 26 11s19-4 26-11l138-139 139 139c7 7 16 11 26 11s19-4 26-11l21-22a35 35 0 000-52z'/%3E %3C/svg%3E") no-repeat center / contain;
  }

.error details :not(summary) {
    color: #666;
  }

.error :first-child {
    margin-top: 0;
  }

.fetching {
  width: 12rem;
  margin: 32vh auto 0 auto;
  font-size: 1.13rem;
  text-align: center
}

.fetching::before {
    /* Spinner */
    display: block;
    width: 4rem;
    height: 4rem;
    margin: 0 auto 2rem auto;
    border-top: 0.3rem solid #f37726;
    border-right: 0.3rem solid transparent;
    border-radius: 50%;
    animation: rotation 1s linear infinite;
    content: "";
  }

.nb-notebook {
  max-width: 45rem;
  margin: 1rem auto 2rem auto;
  padding: 3rem 6rem;
  background-color: white;
  box-shadow: 4px 4px 8px #cfcfcf, -4px -4px 8px #cfcfcf
}

.nb-output table {
  font-size: 0.9em;
}

@keyframes rotation {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@page {
  size: A4;
  margin: 30mm 15mm 15mm 7mm;
}

@media screen and (max-width: 940px) {

.nb-notebook {
    max-width: none;
    margin: 0;
    padding-right: 3rem;
    padding-left: 5rem;
    box-shadow: none
}
}

@media screen and (max-width: 768px) {

.nb-notebook {
    padding: 0.1rem 5% 2rem 5%
}
}

@media (max-width: 40em) {

#header .logo {
      width: 3.8rem;
      margin: 0 0.2rem 0 0.5rem;
      background-size: 6.8rem 1.75rem
  }

#header .file-label {
      margin: 0.6rem
  }

#header .github-link {
      margin: 0 0.5rem
  }

.error {
    height: unset;
    margin: 10vh auto 0 auto;
    padding: 8rem 1rem 0 1rem;
    text-align: center;
    background-position: top center
}
}

@media print {

html,
body {
    color: black;
    background-color: white
}

#header {
    display: none
}

.nb-notebook {
    max-width: none;
    margin: 0;
    padding: 0 0 0 5rem;
    box-shadow: none
}
}

